<template>
    <div v-if="mData!=null">
        <keep-alive>
            <component :is="mData.type" :key="mData.id" v-model="mData" :editData="mData"></component>
        </keep-alive>
    </div>
    <div v-else>
        <h1>请在左侧选择需要配置的问题</h1>
    </div>
</template>

<script>
    import {eventBus} from "../assets/js/eventBus";

    export default {
        name: "componentsSetting",
        components: {
            "wenda": () => import("./WenDaEdit.vue"),
            "danxuan": () => import("./DanXuanEdit.vue"),
            "duoxuan": () => import("./DuoXuanEdit.vue"),
            "shifei": () => import("./ShiFeiEdit.vue"),
            "danxuanqita": () => import("./DanXuanQiTaEdit.vue"),
            "duoxuanqita": () => import("./DuoXuanQiTaEdit.vue"),
            "danxiangdafen": () => import("./DanXiangDaFenEdit.vue"),
            "tiankongjianda": () => import("./TianKongJianDaEdit.vue"),
            "juzhentiankong": () => import("./JuZhenTianKongEdit.vue"),
            "weizhi": () => import("./WeiZhiEdit.vue"),
            //"xialaxuanze": () => import("./XiaLaXuanZeEdit.vue"),
        },
        props:["editData"],
        data() {
            return {
                mData: this.$props.editData
            }
        },
        created() {
            eventBus.$on("dataChange",v=>{
                this.mData=v;
            })
        }
    }
</script>

